<!--
    ===============
     
    ===============
        
     1.  success | warning | error
              2. slotmessage
                 slot
              3. slotbody, slotfooter
              4. @type success | warning | error
              5. @message
 -->
<template >
    <div :class='"picc-result picc-result-" + type' style="margin:0 auto;">
      <div class="picc-result-box">
        <div>
          <i :class='"picc-icon picc-icon-" + type' />
        </div>
        <div class="picc-result-msg">
          {{ message }}
        </div>
        <div class="picc-result-body">
            <slot name="body" />
        </div>
      </div>
        <slot name="footer" />
    </div>
</template>
<script>
export default {
    name: 'Result',
    props: {
        type: {
            type: String,
            required: true,
            default: function() {
                return ''
            }
        },
        message: {
            type: String,
            default: function() {
                return ''
            }
        }

    }
}
</script>
