<!--
    ===============
     
    ===============
        
     1. modal
              2. element ui
                 Modal
              3. @type   | 
              4. @total 
              5. @qrCode  TODO:base64
              6. @feeType 
              7. @countdown 
                 TODO: ;
              8. @orderNo  TODO: slot
 -->
<template id="">
    <div class="picc-qr-payment">
      <div class="picc-qr-payment-head">
        <h2>{{ type == 'alipay' ? '' : ''}}</h2>
        <b class="picc-qr-payment-fee"> {{ total }}</b>
        <b class="picc-qr-payment-fee-type">{{ feeType }}</b>
      </div>
      <div class="picc-qr-payment-img">
        <img :src="qrCode">
      </div>
      <div class="picc-qr-payment-foot">
        <h2 class="picc-qr-payment-expired-time">
          {{ countdown }}
        </h2>
        <b class="picc-qr-payment-expired-tit"></b>
        <b class="picc-qr-payment-order-num"><span></span>{{ orderNo }}</b>
        <el-button type="primary" icon="el-icon-link" round plain @click="copy"></el-button>
      </div>
    </div>
</template>
<script>
export default {
    name: 'QrCodePay',
    props: {
        type: {
            type: String,
            required: true
        },
        total: {
            type: String,
            required: true
        },
        feeType: {
            type: String,
            default: function(){
                return '()'
            }
        },
        qrCode: {
            type: String,
            required: true
        },
        countdown: {
            type: String,
            required: true
        },
        orderNo: {
            type: String,
            required: true
        }
    },
    data () {
        return {}
    },
    methods: {
        copy () {}
    }
}
</script>
